@import url(https://fonts.googleapis.com/css2?family=Inter:wght@400;500;800;900&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: "Inter", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #181818;
  color: white;
}

h1 {
  font-weight: 800;
}

.sll-container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  padding-top: 4em;
  padding-left: 2em;
  padding-right: 2em;
}

.sll-container-children {
  width: 60em;
  max-width: 60em;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.sll-large-text {
  font-weight: normal;
}

.sll-button {
  margin-top: 1em;
  padding-left: 3em;
  padding-right: 3em;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: 3.5em;
  border-radius: 25px;
  border: none;
  background-color: #1cb955;
  font-family: "Inter", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-weight: 600;
  color: white;
  text-transform: uppercase;
  letter-spacing: 1.5px;
}

.sll-button:hover {
  cursor: pointer;
  background-color: #1bd760;
}

